import Vue from 'vue'
import Router from 'vue-router'
// 引进页面组件组件
import Home from '@/components/home.vue';
import ClassList from '@/components/classList.vue';
import MyClass from '@/components/myClass.vue';
import Person from '@/components/person.vue';
import Regster from '@/components/regster.vue';
import ClassInfo from '@/components/classInfo.vue';
Vue.use(Router)


let router = new Router({
  routes: [
    {
    	path:'/home',
    	name:'首页',
    	component:Home
    },
    {
    	path:'/classList',
    	name:'课程列表',
    	component:ClassList
    },
    {
    	path:'/myClass',
    	name:'我的课程',
    	component:MyClass
    },
    {
    	path:'/person',
    	name:'个人中心',
    	component:Person
    },
    {
    	path:'/regster',
    	name:'注册',
    	component:Regster
    },
    {
    	path:'/classInfo',
    	name:'学习页面',
    	component:ClassInfo
    },
    {
    	path:'*',
    	redirect:'/home'
    }
  ]
})

router.beforeEach(function(to,from,next){
	
	var title = to.name;
	document.title = title;
	
	next();
	
})


export default router;


