<template>
	<div class="top-header">
		<span class="left" v-show="isShow" @click="back">返回</span>
		<span class="right">
			{{title}}
		</span>
	</div>
</template>

<script>
	export default {
		name:'Header',
		props:['title','isShow'],
		methods:{
			back(){
				this.$router.go(-1)
			}
		}
	}
</script>

<style>
	.top-header{
		height: 40px;
		line-height: 40px;
		font-size: 20px;
		border-bottom: 1px solid #dddddd;
	}
	.top-header .left{
		float: left;
		width: 20%;
	}
	.top-header .right{
		float: right;
		width: 80%;
		text-align: center;
		text-indent: -60px;
	}
</style>


