<template>
	<div id="person">
		<header>
			<img src="../../build/logo.png"/>
		</header>
		<div class="login">
			<div>
				账户：<input type="text" v-model="user"/>
			</div>
			<div>
				密码：<input type="password" v-model="pass"/>
			</div>
		</div>
		<div>
			<input type="button" value="登录" class="btnLogin" @click="btn"/>
		</div>
	</div>
</template>

<script>
	import data from '@/data/myInfo.js';
	export default{
		name:'Person',
		data(){
			return{
				pass:'',
				user:''
			}
		},
		methods:{
			btn(){
				
				if(this.pass== 123&&this.user=='pl'){
					this.$store.commit('chengedPersonInfe',data)
					
					this.$router.push('/home');
					
				}else{
					alert('账户不存在')
				}
				
			}
		}
	}
</script>

<style>
	#person{
		width: 100vw;
		height: 100vh;
		background: url(../assets/1.jpg);
	}
	#person header{
		width: 100%;
		height: 200px;
		text-align: center;
		padding-top: 70px;
	}
	#person header img{
		width: 77px;
		height: 82px;
	}
	#person .login{
	width: 237px;
	margin: 0 auto;
	height: 80px;
	background: #fff;
	border-radius: 5px;
}
#person .login div{
	height: 40px;
}
#person .login div input{
	border: none;
	outline: none;
}
#person .btnLogin{
	width: 240px;
	height: 40px;
	margin: 0 auto;
	margin-top: 50px;
	display: block;
	color: #fff;
	background: #387ef5;
	font-size: 20px;
	outline: none;
	border: none;
	
}
</style>


