<template>
	<div id="classInfo">
		<Header title="学习页面" :isShow="true"></Header>
		<div class="vide">
			<video src="">
				
			</video>
		</div>
		<ul class="evaluate">
			<li class="active">视频</li>
			<li>评价</li>
		</ul>
		<h3 class="title">课程详情</h3>
		<div class="content">
			<p>课程名称:{{info.name}}</p>
			<p>课程价格:{{info.price}}</p>
			<p>课程描述:{{info.describe}}</p>
			
		</div>
	</div>
</template>

<script>
	import Header from '@/components/header/header.vue';
	import data from '@/data/data.js';
	
	export default {
		name:'ClassInfo',
		components:{
			Header
		},
		data(){
			return{
				info:{}
			}
		},
		created(){
			
			
			this.info = data.filter((item)=>{
				return item.id == this.$route.query.id
			})[0]
			
			// 调用函数，把vuex里面的数据onoff变成false
			this.$store.commit('chengedOnoffFalse')
			
			console.log(this.$store.state.onoff)
		}
	}
</script>

<style>
	#classInfo{
		padding: 0 3px;
		padding-bottom: 70px;
	}
	#classInfo .vide{
		height: 180px;
	}
	#classInfo .vide video{
		width: 100%;
		height: 100%;
		background: #000;
	}
	#classInfo .evaluate{
		display: flex;
		height: 33px;
	}
	#classInfo .evaluate li{
		list-style: none;
		text-align: center;
		height: 33px;
		line-height: 33px;
		flex: 1;
		border: 1px solid #ddd;
		margin: 0 !important;
	}
	#classInfo .evaluate li.active{
		color: red;
	}
	#classInfo .title{
		border: 1px solid #ddd;
		margin: 0;
		padding-left: 10px;
		font-size: 14px;
		line-height: 32px;
		height: 32px;
	}
	#classInfo .content{
		padding-left: 12px;
	}
	#classInfo .content p{
		line-height: 24px;
		margin: 0;
	}
</style>


