<template>
	<div id="home">
		<Header title="首页" :isShow="false"></Header>
		<swiper class="top-swiper" :options="swiperOption">
			<swiperSlide v-for="(item,index) in list" :key="index">
				<img :src="item.image"/>
			</swiperSlide>
			 <div class="swiper-pagination"  slot="pagination"></div>
		</swiper>
		<h3 class="title">最新课程</h3>
		<div class="content">
			<div class="item" v-for="item in list" @click="btnHash(item.id)">
				<img :src="item.image" alt="" />
				<h3>{{item.name}}</h3>
				<p>{{item.describe|contentFilter}}</p>
			</div>
		</div>
	</div>
</template>

<script>
	
	import Header from '@/components/header/header.vue';
	import {swiper, swiperSlide} from 'vue-awesome-swiper';
	import data from '@/data/data.js';
	
	export default {
		name:'Home',
		components:{
			Header,
			swiper, 
			swiperSlide
		},
		data(){
			return {
				list:data,
				swiperOption:{
					loop:true,
					pagination: {
					    el: '.swiper-pagination',
					  
					}
				}
			}
		},
		filters:{
			contentFilter(value){
				return value.substring(0,20);
			}
		},
		methods:{
			btnHash(id){
				this.$router.push({
					path:'/classInfo',
					query:{
						id:id
					}
				})
				
			}
		}
	}
</script>

<style>
	*{
		padding: 0;
		margin: 0;
	}
	#home{
		padding-bottom: 70px;
	}
	#home .top-swiper{
		width: 100%;
		height: 150px;
	}
	#home .top-swiper img{
		width: 100%;
		height: 150px;
		vertical-align: top;
		border: none;
	}
	#home .top-swiper .swiper-pagination-bullet-active{
		background: green;
	}
	#home .title{
		font-size: 20px;
		line-height: 40px;
		height: 40px;
		padding-left: 14px;
		margin: 0;
		border-bottom: 1px solid #dddddd;
	}
	#home .content{
		width: 100%;
		overflow: hidden;
	}
	#home .content .item{
		width: 50%;
		float: left;
		height: 207px;
	}
	#home .content .item img{
		width: 86px;
		height: 120px;
		display: block;
		margin: 30px auto 0;	
	}
	#home .content .item h3{
		font-size: 12px;
		text-align: center;
		line-height: 24px;
		margin: 0;
	}
	#home .content .item p{
		font-size: 12px;
		text-align: center;
		
	}
</style>


