<template>
	<div id="myClass">
		<Header title="我的课程"></Header>
		<div class="login" v-if="!isLogin">请你登录</div>
		<div class="content" v-show="showData">
			<div class="item" v-for="item in showData">
				<div class="left">
					<img :src="item.image" alt="" />
				</div>
				<div class="right">
					<h3>{{item.name}}</h3>
					<p>{{item.describe}}</p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import Header from '@/components/header/header.vue';
	export default {
		name:'MyClass',
		components:{
			Header
		},
		data(){
			return{
				
			}
		},
		computed:{
			isLogin(){
				return this.$store.state.personInfe.name?true:false;
			},
			showData(){
				console.log()
				
				return this.$store.state.personInfe.shoping
			}
		}
	}
</script>

<style>
	#myClass .login{
		margin-top: 100px;
		height: 40px;
		line-height: 40px;
		text-align: center;
		font-size: 30px;
	}
	#myClass .content{
		padding: 0 5px;
	}
	#myClass .content .item{
		border: 1px solid #ddd;
		overflow: hidden;
	}
	#myClass .content .item .left{
		width: 111px;
		float: left;
		height: 104px;
	}
	#myClass .content .item .left img{
		width: 52px;
		height: 74px;
		text-align: center;
		margin-bottom: 17px;
	}
	#myClass .content .item .right{
		width: 197px;
		float: left;
		height: 104px;
	}
	#myClass .content .item .right h3{
		height: 46px;
		line-height: 46px;
		font-size: 12px;
		margin: 0;
	}
	#myClass .content .item .right p{
		line-height: 17px;
		margin: 0;
	}
</style>


