<template>
	<div id="classList">
		<Header title="课程列表"></Header>
		<form class="search">
			<input type="text" placeholder="搜索课程" v-model="search"/>
			<button class="glyphicon glyphicon-search" @click="searchList"></button>
		</form>
		
		<div class="content" v-show="showList.length">
			<div class="item" v-for="item in showList" @click="btnInfo(item.id)">
				<div class="left">
					<img :src="item.image" alt="" />
				</div>
				<div class="right">
					<h3>{{item.name}}</h3>
					<p>{{item.describe|listFilter}}</p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import Header from '@/components/header/header.vue';
	import data from '@/data/data.js';
	export default {
		name:'ClassList',
		components:{
			Header
		},
		data(){
			return{
				showList:[],
				search:''
			}
		},
		methods:{
			searchList(){
				this.showList = data.filter((item,index)=>{
					return item.name == this.search
				})
			},
			btnInfo(id){
				
				this.$router.push({
					path:'/classInfo',
					query:{
						id:id
					}
				})
			}
		},
		filters:{
			listFilter(value){
				return value.substring(0,20)
			}
		}
	}
</script>

<style>
	#classList .search{
		height: 47px;
		
		padding-left: 10px;
		border-top:none;
		margin-bottom: 20px;
	}
	#classList .search input{
		border: none;
		outline: none;
		border: 1px solid #ddd;
		width: 80%;
		float: left;
		height: 45px;
		padding-left: 30px;
	}
	#classList .search button{
		width: 18%;
		height: 47px;
		float: left;
		vertical-align: top;
		border: none;
		background: cornflowerblue;
		margin-top: -1px;
	}
	#classList .content{
		padding: 0 5px;
	}
	#classList .content .item{
		border: 1px solid #ddd;
		overflow: hidden;
	}
	#classList .content .item .left{
		width: 111px;
		float: left;
		height: 104px;
	}
	#classList .content .item .left img{
		width: 52px;
		height: 74px;
		text-align: center;
		margin-bottom: 17px;
	}
	#classList .content .item .right{
		width: 197px;
		float: left;
		height: 104px;
	}
	#classList .content .item .right h3{
		height: 46px;
		line-height: 46px;
		font-size: 12px;
		margin: 0;
	}
	#classList .content .item .right p{
		line-height: 17px;
		margin: 0;
	}
</style>


