<template>
  <div id="app">
    <router-view></router-view>
    <!--路由导航-->
    <ul class="nav">
				<router-link to="/home" tag="li">
					<span class="glyphicon glyphicon-home"></span><br />
					<span>首页</span>
				</router-link>
				<router-link to="/classList" tag="li">
					<span class="glyphicon glyphicon-list"></span><br />
					<span>课程列表</span>
				</router-link>
				<router-link to="/myClass" tag="li">
					<span class="glyphicon glyphicon-folder-open"></span><br />
					<span>我的课程</span>
				</router-link>
				<router-link to="/person" tag="li">
					<span class="glyphicon glyphicon-user"></span><br />
					<span>个人中心</span>
				</router-link>
			</ul>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
*{
				padding: 0;
				margin: 0;
			}
			#app{
				width: 100vw;
				height: 100vh;
			}
			.nav{
				position: fixed;
				left: 0;
				bottom: 0;
				width: 100%;
				height: 50px;
				border-top:1px solid #ebebeb;
				background: #fff;
			}
			.nav li{
				list-style: none;
				width: 25%;
				float: left;
				height: 100%;
				padding-top: 8px;
				text-align: center;
				font-size: 13px;
			}
			.nav li span{
				color: #4d555d;
			}
			.nav .router-link-active span{
				color: #387ef5;
			}
</style>



